const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2]
const trans1 = (x) => `translate3d(${x / 10}px,0px,0)`
const trans2 = (x) => `translate3d(${x / 60}px,0px,0)`

export function SolTowers() {
  
  const { height, width } = useWindowDimensions();

  return (
    <div style={{height:'400px',width:350, position:'absolute', top:isMobile() ? height-350: height-550, left:width/2-155,overflow:'hidden',overflowY:'hidden', pointerEvents:'none'}}>
      <div style={{ position:'absolute', marginLeft:'10px'}}>
        <Towers/>
      </div>
      
    </div>
  )
}

export function Cityscape() {
  const [x, setX] = useState()
  const [props, set] = useSpring(() => ({ xy: [0, 0], config: { mass: 5, tension: 500, friction: 500 } }))
  const { height, width } = useWindowDimensions();

  return (
    <div style={{height: isMobile() ? '225px':'400px', position:'relative', top:isMobile() ? height-225: height-400,left:'0px',overflow:'hidden',overflowY:'hidden'}} onMouseMove={({ clientX: x, clientY: y }) => set({ xy: calc(x, y) })}>
      
      <animated.div style={{ transform: props.xy.interpolate(trans1) }}>
        <Cityimg/>
      </animated.div>

    </div>
  )
}

export default function App() {
  const { height, width } = useWindowDimensions();
  return (
    <div style={{position:'absolute',height:height, width:'100%', backgroundColor:'black', overflowY:'hidden'}}>
      
      <Canvas dpr={[1, 1.5]} camera={{ position: [0, 0, 30] }}>
        <color attach="background" args={['black']} />
        
        <OrbitControls enableZoom={false} enablePan={false} enableRotate={false} />
        <Suspense fallback={null}>
          <Rig>            
            <Dot position={[0, -.5, -.001]}/>          
          </Rig>
          
          <EffectComposer multisampling={4}>
            <Bloom kernelSize={3} luminanceThreshold={0} luminanceSmoothing={0.4} intensity={0.6} />
            <Bloom kernelSize={5} luminanceThreshold={0} luminanceSmoothing={0} intensity={0.5} />
          </EffectComposer>
        </Suspense>
        
      </Canvas>
    </div>
    
  )
}
